body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.slider-container {
    width: 3rem;
    height: 10rem;
    border: 1px solid rgb(82, 82, 82);
    border-width: 0 1px;
    background-color: #252525;
    display: flex;
    flex-direction: column-reverse;
}

.value-display{
    background-color: #0084A1;
    width: 100%;
} 
.play-button {
  border: 0;
  background: transparent;
  box-sizing: border-box;
  width: 0;
  height: 74px;
  border-color: transparent transparent transparent #202020;
  transition: 100ms all ease;
  cursor: pointer;
  border-style: solid;
  border-width: 37px 0 37px 60px; }
  .play-button.paused {
    border-style: double;
    border-width: 0px 0 0px 60px; }
  .play-button:hover {
    border-color: transparent transparent transparent #404040; }

body {
    background-color: #3B3B3B
}

.page {
    padding: 0 10%
}

.page-title {
    color: white;
    text-align: center
}

.subtitle {
    color: #FF6F61
}

.body-text {
    color: white
}

strong {
    color: #FF6F61
}

.footer {
    color: rgb(175, 175, 175);
    text-align: center;
    font-size: 0.9rem;
}

.githubIcon {
    width: 40px;
    height:40px;
}
.value-text {
    color: white;
    font-size: 0.7rem;
    margin-top: 0px;
    margin-left: auto;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 0;
    right: 0;
    pointer-events: none;

}

.knob-container {
    display: inline-block;
    /* position: relative; */
    text-align: center;
    margin: 0.5em;
}

.label {
    color: white;
    font-size: 0.7rem;
    position: relative;
    top: -15px;
    pointer-events: none;
}
.interaction-container{
    background-color: #252525;
    width: 100vw;
    position: 'relative';
    margin-left: -200px;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 300px;
}

.interaction-text{
    margin-top: 0px;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    color: #8F8F8F;
    margin-left: -60px;
}
